import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '//panel',
            name: 'home',
            component: () => import('../components/Home.vue'),
            meta: {
                title: 'Home',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/accounts',
            name: 'accounts',
            component: () => import('../components/Accounts.vue'),
            meta: {
                title: 'Accounts',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/accounts/add',
            name: 'accounts',
            component: () => import('../components/AddAccount.vue'),
            meta: {
                title: 'Add Account',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/accounts/:id',
            name: 'edit account',
            component: () => import('../components/EditAccount.vue'),
            meta: {
                title: 'Edit Account',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/logs',
            name: 'logs',
            component: () => import('../components/Logs.vue'),
            meta: {
                title: 'Logs',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/plans',
            name: 'plans',
            component: () => import('../components/Plans.vue'),
            meta: {
                title: 'Plans',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/plans/add',
            name: 'plans',
            component: () => import('../components/AddPlan.vue'),
            meta: {
                title: 'AddPlan',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/plans/:id',
            name: 'plans',
            component: () => import('../components/EditPlan.vue'),
            meta: {
                title: 'EditPlan',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/orders',
            name: 'orders',
            component: () => import('../components/Orders.vue'),
            meta: {
                title: 'Orders',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/orders/add',
            name: 'orders',
            component: () => import('../components/AddOrder.vue'),
            meta: {
                title: 'AddOrder',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/orders/:id',
            name: 'orders',
            component: () => import('../components/EditOrder.vue'),
            meta: {
                title: 'EditOrder',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/news',
            name: 'news',
            component: () => import('../components/News.vue'),
            meta: {
                title: 'News',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/news/add',
            name: 'news',
            component: () => import('../components/AddArticle.vue'),
            meta: {
                title: 'Add Article',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/news/:id',
            name: 'news',
            component: () => import('../components/EditArticle.vue'),
            meta: {
                title: 'Edit Article',
                requiresAuth: true,
            }
        },
        {
            path: '/panel/login',
            name: 'login',
            component: () => import('../components/Login.vue'),
            meta: {
                title: 'Login'
            }
        }
    ],
    scrollBehavior() {
        return {x: 0, y: 0}
    }
})

