export default {
    state: {
        alert: {
            'active': false,
            'color': 'error',
            'text': ''
        }
    },
    mutations: {
        setAlert(state, alert) {
            state.alert.active = alert.active
            state.alert.color = alert.color
            state.alert.text = alert.text
        }
    },
    actions: {
        setAlert({commit}, alert) {
            commit('setAlert', alert)
        },
    },
    getters: {
        alert: state => state.alert
    }
}